import { fetchChartData } from '@engine/chart';
import { ChartNewCellEventArgs } from '../cell/cell.chart_new';
import { AbstractCellHandler } from './handler.abstract';

export class TableChartCellFetchHandler extends AbstractCellHandler {
    public eventNames = ['CELL_CHART_FETCH_DATA'];

    public name = 'CELL_CHART_FETCH_DATA';

    public merger = 'covered';

    public async action({ requestParams }: TableCellSetValueHandler) {
        return fetchChartData(requestParams);
    }
}

export type TableCellSetValueHandler = Parameters<AbstractCellHandler['action']>[0] & ChartNewCellEventArgs
